.about-us {
  box-sizing: border-box;
  padding-top: 30px;
  padding-left: 30px;
  padding-right: 30px;
}
@media (min-width: 768px) {
  .about-us {
    margin: 0 auto;
    width: inherit;
    padding-left: 50px;
    padding-right: 50px;
    padding-top: 56px;
  }
}
@media (min-width: 1150px) {
  .about-us {
    max-width: 1200px;
    margin: 0 auto;
    box-sizing: border-box;
    padding-top: 68px;
  }
}
@media (min-width: 1150px) {
  .about-us__wrapper {
    padding-left: 175px;
    padding-right: 200px;
  }
}
.about-us__title {
  margin: 0;
  margin-bottom: 29px;
}
@media (min-width: 768px) {
  .about-us__title {
    margin-bottom: 98px;
  }
}
@media (min-width: 1150px) {
  .about-us__title {
    margin-bottom: 96px;
  }
}
.advantages {
  box-sizing: border-box;
}
@media (min-width: 768px) {
  .advantages {
    display: grid;
    grid-template-columns: 1fr 1fr;
  }
}
@media (min-width: 1150px) {
  .advantages {
    column-gap: 23px;
  }
}
.advantages__item {
  font-weight: 700;
  font-size: 17px;
  line-height: 20px;
  color: #000000;
}
@media (min-width: 768px) {
  .advantages__item {
    font-size: 20px;
    line-height: 22px;
  }
  .advantages__item:nth-of-type(6) {
    order: 6;
  }
  .advantages__item:nth-of-type(5) {
    order: 4;
  }
  .advantages__item:nth-of-type(4) {
    order: 2;
  }
  .advantages__item:nth-of-type(3) {
    order: 5;
  }
  .advantages__item:nth-of-type(2) {
    order: 3;
  }
  .advantages__item:nth-of-type(1) {
    order: 1;
  }
}
.advantages__item--eco {
  margin-bottom: 28px;
}
@media (min-width: 768px) {
  .advantages__item--eco {
    position: relative;
    margin-bottom: 110px;
  }
}
.advantages__item--money {
  margin-bottom: 33px;
}
@media (min-width: 768px) {
  .advantages__item--money {
    position: relative;
    margin-bottom: 110px;
  }
}
.advantages__item--heart {
  margin-bottom: 31px;
}
@media (min-width: 768px) {
  .advantages__item--heart {
    position: relative;
  }
}
.advantages__item--wool-ball {
  margin-bottom: 32px;
}
@media (min-width: 768px) {
  .advantages__item--wool-ball {
    position: relative;
    margin-bottom: 110px;
    padding-left: 1px;
  }
}
.advantages__item--airplane {
  position: relative;
  padding-top: 51px;
  margin-bottom: 29px;
}
@media (min-width: 768px) {
  .advantages__item--airplane {
    padding-top: 0;
    margin-bottom: 111px;
  }
}
.advantages__item--gift {
  position: relative;
  margin-bottom: 30px;
}
@media (min-width: 768px) {
  .advantages__item--gift {
    padding-top: 0;
  }
}
.advantages__item::before {
  display: block;
}
.advantages__item--eco::before {
  width: 33px;
  height: 38px;
  content: url("../img/eco.svg");
  padding-left: 8px;
  margin-bottom: 18px;
}
@media (min-width: 768px) {
  .advantages__item--eco::before {
    position: absolute;
    top: -57px;
    left: 0;
  }
}
.advantages__item--money::before {
  width: 47px;
  height: 27px;
  content: url("../img/money.svg");
  margin-bottom: 28px;
}
@media (min-width: 768px) {
  .advantages__item--money::before {
    position: absolute;
    top: -54px;
    left: 0;
  }
}
.advantages__item--heart::before {
  width: 34px;
  height: 27px;
  content: url("../img/heart.svg");
  padding-left: 7px;
  margin-bottom: 22px;
}
@media (min-width: 768px) {
  .advantages__item--heart::before {
    position: absolute;
    top: -48px;
    left: 1px;
  }
}
.advantages__item--wool-ball::before {
  width: 35px;
  height: 34px;
  content: url("../img/wool_ball.svg");
  padding-left: 3px;
  margin-bottom: 18px;
}
@media (min-width: 768px) {
  .advantages__item--wool-ball::before {
    position: absolute;
    top: -53px;
    left: 0;
  }
}
.advantages__item--airplane::before {
  width: 55px;
  height: 23px;
  content: url("../img/airplane.svg");
  position: absolute;
  margin-bottom: 28px;
  top: 0;
  left: -6px;
}
@media (min-width: 768px) {
  .advantages__item--airplane::before {
    top: -51px;
  }
}
.advantages__item--gift::before {
  width: 33px;
  height: 32px;
  content: url("../img/gift.svg");
  padding-left: 3px;
  margin-bottom: 22px;
}
@media (min-width: 768px) {
  .advantages__item--gift::before {
    position: absolute;
    top: -54px;
    left: 0;
  }
}
.button {
  display: block;
  box-sizing: border-box;
  min-height: 52px;
  padding-top: 14px;
  padding-bottom: 14px;
  font-weight: 700;
  font-size: 18px;
  line-height: 20px;
  text-align: center;
  text-transform: uppercase;
  text-decoration: none;
  color: #444444;
  background-color: #ffffff;
  border: 2px solid #444444;
  outline: 0;
}
.button:hover {
  background-color: #444444;
  color: #ffffff;
}
.button:active {
  background-color: #444444;
  color: rgba(255, 255, 255, 0.3);
}
.button:focus {
  outline: #56C3B2 auto 2px;
}
.cart {
  text-decoration: none;
  color: #000000;
  font-weight: 700;
  font-size: 17px;
  line-height: 24px;
  margin: 0;
  position: relative;
}
.cart:active {
  color: rgba(0, 0, 0, 0.3);
}
.cart:hover {
  color: rgba(0, 0, 0, 0.6);
}
.cart:focus {
  outline: #62D1BA auto 2px;
  opacity: 1;
}
.cart:hover .cart__icon {
  opacity: 1;
  fill: #63D1BB;
}
.cart:active .cart__icon {
  opacity: 0.3;
  fill: #63D1BB;
}
.cart__icon {
  opacity: 0.2;
  position: absolute;
  left: 0;
  top: 2px;
}
@media (min-width: 768px) {
  .cart__icon {
    left: -12px;
    top: 1px;
  }
}
.cart__text {
  margin: 0;
}
.catalog__wrapper {
  box-sizing: border-box;
  padding-top: 29px;
  padding-right: 30px;
  padding-left: 30px;
  padding-bottom: 30px;
}
@media (min-width: 768px) {
  .catalog__wrapper {
    padding-top: 69px;
    padding-right: 50px;
    padding-left: 50px;
    padding-bottom: 53px;
  }
}
