@media (min-width: 768px) {
  .categories {
    background-color: #F9F9F9;
    position: relative;
  }
}
@media (min-width: 768px) {
  .categories::after {
    height: 24px;
    width: 100vw;
    content: "";
    background-image: url("../img/zigzag_fill_desktop.svg");
    background-repeat: repeat;
    position: absolute;
    bottom: -11px;
  }
}
@media (min-width: 1150px) {
  .categories::after {
    background-image: url("../img/zigzag-new-fill-desktop.svg");
    height: 34px;
    bottom: -15px;
  }
}
@media (min-width: 768px) {
