.category {
  box-sizing: border-box;
  position: relative;
}
.category--interior {
  background-color: #63D1BB;
}
.category--toys {
  background-color: #56C3B2;
}
.category__link {
  text-decoration: none;
  font-weight: 700;
  font-size: 19px;
  line-height: 24px;
  color: #ffffff;
  box-sizing: border-box;
  padding-left: 30px;
  padding-right: 30px;
  min-height: 103px;
  margin: 0 auto;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding-bottom: 8px;
}
@media (min-width: 768px) {
  .category__link {
    width: inherit;
    min-height: 119px;
    padding-left: 36px;
    padding-right: 36px;
    justify-content: space-between;
    font-size: 20px;
    line-height: 30px;
    padding-bottom: 10px;
  }
}
.category__link:hover,
.category__link:active {
  min-height: 124px;
  margin-top: -5px;
}
.category__link--interior:hover::after,
.category__link--toy:hover::after {
  opacity: 0.7;
}
.category__link--interior:active::after,
.category__link--toy:active::after {
  opacity: 0.3;
}
.category__link--interior::after {
  content: url("../img/interior.svg");
  width: 40px;
  height: 62px;
  position: absolute;
  top: 23px;
  right: 36px;
  opacity: 0.5;
}
@media (min-width: 768px) {
  .category__link--interior::after {
    top: 29px;
    right: 37px;
  }
}
.category__link--toy::after {
  content: url("../img/toy.svg");
  width: 60px;
  height: 60px;
  position: absolute;
  top: 21px;
  right: 28px;
  opacity: 0.5;
}
@media (min-width: 768px) {
  .category__link--toy::after {
    top: 30px;
    right: 34px;
  }
}
